import Vue from 'vue'
import Router from 'vue-router'

const undia = () => import(/* webpackChunkName: 'undiagnosed' */ '../components/common/undiagnosed.vue')
const dia = () => import(/* webpackChunkName: 'dia' */ '../components/common/diagnosed.vue')
const undiaWechat = () => import(/* webpackChunkName: 'undiaWechat' */ '../components/common/undiaWechat.vue')
const diaWechat = () => import(/* webpackChunkName: 'diaWechat' */ '../components/common/diaWechat.vue')
const login = () => import(/* webpackChunkName: 'dia' */ '../components/common/login.vue')
const home = () => import(/* webpackChunkName: 'dia' */ '../components/common/home.vue')
const report = () => import(/* webpackChunkName: 'report' */ '../components/common/report.vue')
const inform = () => import(/* webpackChunkName: 'inform' */ '../components/common/inform.vue')
const informWechat = () => import(/* webpackChunkName: 'informWechat' */ '../components/common/informWechat.vue')

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login,
      hidden: true
    },
    {
      path: '/',
      name: 'home',
      component: home,
      beforeEnter: (to, from, next) => {
        const token = localStorage.getItem('token');
        if (token) {
          next()
        } else {
          next('/')
        }
      },
      children: [
        { path: '/undia', component: undia, name: 'undia' },
        { path: '/dia', component: dia, name: 'dia' },
        { path: '/undiaWechat', component: undiaWechat, name: 'undiaWechat' },
        { path: '/diaWechat', component: diaWechat, name: 'diaWechat' },
        {path: '/report', component: report, name: 'report'},
        {path: '/inform', component: inform, name: 'inform'},
        {path: '/informWechat', component: informWechat, name: 'informWe'},
      ]
    },

    {
      path: '*',
      hidden: true,
      redirect: { path: '/' }
    }
  ],
})





