import Vue from 'vue'
import Vuex from 'vuex'
import cookie from '../static/js/cookie';

Vue.use(Vuex);

const state={
  patient_info:{},
  page_info:{},
  user_info:{},
  page_from:'',
  undiagnosed:false,
  diagnosed:false,
  transform:false,
  statistics:false,
  report:false,
  leftNav:false,
  topHeader:false,
  login:true,
  loginPage:true,
  hospital_id:0,
  checked:false,
  flag:false,
  position:false,
  we_notification:false,
  com_notification:false,
  total_notification:false,
  super_show:false
};

const mutations={
  clickUndiagnosed(state){
    state.undiagnosed = true;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = false;
  },
  clickDiagnosed(state){
    state.undiagnosed = false;
    state.diagnosed = true;
    state.transform = false;
    state.statistics = false;
    state.report = false;
  },
  clickStatistics(state){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = false;
    state.report = false;
    state.statistics = true;
  },
  clickTransform(state){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = true;
    state.statistics = false;
    state.report = false;
  },

  clickReport(state,patient_info){
    state.undiagnosed = false;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = true;
    state.patient_info = patient_info;
  },

  backToUndiagnosed(state){
    state.undiagnosed = true;
    state.diagnosed = false;
    state.transform = false;
    state.statistics = false;
    state.report = false;
  },

  rememberPageAndSize(state,page_info){
    state.page_info = page_info;
  },

  setInfo(state,checked){
    state.user_info = {
      name:cookie.getCookie('name'),
      token:cookie.getCookie('token')
    };
    state.checked=checked;
    console.log('checked',checked)
  },
  saveHospitalId(state,hospital_id){
    state.hospital_id = hospital_id;
    console.log('state.hospital',hospital_id)
  },

  login(state){
    state.loginPage = false;
  },


  changeFlag(state,flag){
    state.flag = flag;
  },

  changeSuperShow(state,value){
    state.super_show = value;
  },

  changeNotification(state,notification){
    state.com_notification = notification['com_notification'];
    state.we_notification = notification['we_notification'];
    state.total_notification = notification['total_notification'];
  },

  changePageFrom(state,value){
    state.page_from = value
  }

};

export default new Vuex.Store({
  state,
  mutations
})



